<template>
	<div class="ratings" ref="rateScroll">
		<div class="ratings-content">
			<div class="overview">
				<div class="overview-left">
					<h1 class="score">{{seller.score}}</h1>
					<div class="title">综合评分</div>
					<div class="rank">高于周边商家{{seller.rankRate}}%</div>
				</div>
				<div class="overview-right">
					<div class="wrapper score-wrapper">
						<span class="title">服务态度</span>
						<star :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
					</div>
					<div class="wrapper food-wrapper">
						<span class="title">商品评分</span>
						<star :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="delivery-wrapper">
						<span class="title">送达时间</span>
						<span class="delivery">{{seller.deliveryTime}}分钟之内送达</span>
					</div>
				</div>
			</div>
			<split></split>
			<ratingselect v-on:types="foodType" v-on:toggles="foodToggle" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="ratings"></ratingselect>
			<div class="ratings-wrapper">
				<ul>
					<li v-for="rating in ratings" class="rating-item">
						<div class="avatar">
							<img width="28" height="28" :src="rating.avatar">
						</div>
						<div class="content">
							<h1 class="name">{{rating.username}}</h1>
							<div class="star-wrapper">
								<star :size="24" :score="rating.score"></star>
								<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
							</div>
							<p class="text">{{rating.text}}</p>
							<div class="recommend" v-show="rating.recommend.length">
								<span class="icon-thumb_up"></span>
								<span class="item" v-for="item in rating.recommend">{{item}}</span>
							</div>
							<div class="time">
	  							{{rating.rateTime | formatDate}}
	  						</div>
						</div>
					</li>
				</ul>
			</div>
		</div>	
	</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import star from '../../components/star/star';
import split from '../split/split';
import ratingselect from '../ratingselect/ratingselect';
import {formatDate} from '../../common/js/date';
const ALL = 2;
const ERR_OK = 0;
export default {
	props: {
		seller: {
			type: Object
		}
	},
	methods: {
		foodToggle(onlyContent) {
			console.log(onlyContent);
			this.onlyContent = onlyContent;
		},
		foodType(type) {
			this.selectType = type;
		}
	},
	data() {
		return {
			ratings: [],
			selectType: ALL,
			onlyContent: true,
			desc: {
				all: '全部',
				positive: '推荐',
				negative: '吐槽'
			}
		};
	},
	created() {
		this.$http.get('/api/ratings').then((response) => {
          response = response.body;
          if (response.errno === ERR_OK) {
				this.ratings = response.data;
				this.$nextTick(() => {
					this.scroll = new BScroll(this.$refs.rateScroll,{
					click: true
				});
			}); 
          }
		});
	},
	filters: {
		formatDate(time) {
			let date = new Date(time);
			return formatDate(date,'yyyy-MM-dd hh:mm');
		} 
	},
	components: {
	star,
	split,
	ratingselect
	}
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl";
.ratings{
	position: fixed;
	top: 174px;
	bottom: 0;
	width: 100%;
	left: 0;
	overflow: hidden;
	.overview{
		display: flex;
		padding: 18px 0;
		.overview-left{
		   flex:0 0 127px;
		   width: 127px;
		   border-right: 1px solid rgba(7,17,27,0.1);
		   text-align: center;
		   padding: 6px 0;
		   @media only screen and (max-width:320px){
		   	 flex:0 0 100px;
		   	 width: 100px;
		   }
		   .score{
		   	font-size: 24px;
		   	line-height: 28px;
		   	margin-bottom: 6px;
		   	color: rgb(255,153,0);
		   }
		   .title{
		   	margin-bottom: 8px;
		   	line-height: 12px;
			font-size: 12px;
			color: rgb(7,17,27);
		   }
		   .rank{
		   	line-height: 10px;
		   	font-size: 10px;
		   	color: rgb(147,153,159);
		   }
		}
		.overview-right{
			flex:1;
			padding: 6px 0 6px 12px;
			@media only screen and (max-width:320px){
		   	  padding: 6px;
		    }
			.wrapper{
				margin-bottom: 8px;
				font-size: 0;
				.title{
					font-size: 12px;
					color: rgb(7,17,27);
					line-height: 18px;
					display: inline-block;
					vertical-align: top;
				}
				.star{
					display: inline-block;
					vertical-align: top;
					margin: 0 3px;
				}
				.score{
					display: inline-block;
					vertical-align: top;
					font-size: 12px;
					margin-left: 4px;
					line-height: 18px;
					color: rgb(255,153,0);
				}
			}
			.delivery-wrapper{
				font-size: 0;
				.title{
					font-size: 12px;
					color: rgb(7,17,27);
					line-height: 18px;
				}
				.delivery{
					margin-left: 12px;
					font-size: 12px;
		            color: rgb(147,153,159);
				}
			}	
		}
	}
	.ratings-wrapper{
		padding: 0 18px;
		.rating-item{
			display: flex;
			padding: 18px 0;
			border-1px(rgba(7,17,27,0.1));
			.avatar{
				flex:0 0 28px;
				width:28px;
				margin-right: 12px; 
				img{
					border-radius: 50%;
				}
			}
			.content{
				position: relative;
				flex:1;
				.name{
					margin-bottom: 4px;
					line-height: 12px;
					font-size: 10px;
					color: rgb(7,17,27);
				}	
				.star-wrapper{
					margin-bottom: 6px;
					font-size: 0;
					line-height: 18px;
					.star{
						font-size: 24px;
						display: inline-block;
						margin-right: 6px;
					}
					.delivery{
						font-size: 12px;
						display: inline-block;
						color: rgb(147,153,159);
					}
				}	
				.text{
				    line-height: 18px;
				    font-size: 12px;
				    color:rgb(7,17,27);
				    font-size: 12px;
				}	
				.recommend{
					line-height: 16px;
					font-size: 0;
					margin-top: 4px;
					.icon-thumb_up,.item{
						display: inline-block;
						margin-top: 0 8px 4px 0;
						font-size: 9px;
					}
					.icon-thumb_up{
						color: rgb(0,160,220);
					}
					.item{
						padding: 0 6px;
						border:1px solid rgba(7,17,27,0.1);
						border-radius: 1px;
						color: rgb(147,153,159);
						background: #fff;
					}
				}
				.time{
					position: absolute;
					top:0;
					right: 0;
					line-height: 12px;
					font-size: 10px;
					color: rgb(147,153,159);
				}
			}		
		}
	}
}

</style>




