<template>
  <div class="ratingselect">
  	<div class="rating-type border-1px">
  		<span @click="select(2,$event)" class="block positive" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
  		<span @click="select(1,$event)" class="block positive" :class="{'active':selectType===1}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
  		<span @click="select(0,$event)" class="block negative" :class="{'active':selectType===0}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
  	</div>
  	<div @click="toggleContent($event)" class="switch" :class="{'on':onlyContent===true}">
  		<span class="icon-check_circle"></span>
  		<span class="text">只能看到内容的评价</span>
  	</div>
  </div>
</template>

<script type="text/ecmascript-6">
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
	props: {
		ratings: {
			type: Array,
				default() {
				return [];
			}
		},
		selectType: {
			type: Number,
			default: ALL
		},
		onlyContent: {
			type: Boolean,
			default: false
		},
		desc: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	methods: {
		select(type,event) {
			if (!event._constructed) {
				return;
			}
			this.selectType = type;
			this.$emit('types',type);
		},
		toggleContent(event) {
			if (!event._constructed) {
				return;
			}
			this.onlyContent = !this.onlyContent;
			this.$emit('toggles',this.onlyContent);
		}
	},
	computed: {
		positives() {
			return this.ratings.filter((rating) => {
				return rating.rateType === POSITIVE;
			});
		},
		negatives() {
			return this.ratings.filter((rating) => {
				return rating.rateType === NEGATIVE;
			});
		}
	}
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl";
.ratingselect{
	.rating-type{
		padding: 18px 0;
		margin: 0 18px;
		border-1px(rgba(7,17,27,0.1));
		font-size: 0;
		.block{
			display: inline-block;
			padding: 8px 12px;
			font-size: 12px;
			border-radius: 2px;
			margin-right: 8px;
			line-height: 16px;
			color: rgb(77,85,93);
			.count{
				font-size: 8px;
				margin-left: 2px;
			}
			&.active{
				color:#fff;
			}
			&.positive{
				background: rgba(0,160,220,0.2);
				&.active{
				   background: rgb(0,160,220);
			    }
			}
			&.negative{
				background: rgba(77,85,93,0.2);
				&.active{
				   background: rgb(0,160,220);
			    }
			}
		}
	}
	.switch{
		padding: 12px 18px;
		line-height: 24px;
		border-bottom: 1px solid rgba(7,17,27,0.1);
		color: rgb(147,153,159);
		font-size: 0;
		&.on{
			.icon-check_circle{
		      color: #00c850;
			}
		}
		.text{
			font-size: 12px;
			display: inline-block;
			vertical-align: top;
		}
		.icon-check_circle{
			display: inline-block;
			vertical-align: top;
			font-size: 24px;
			margin-right: 4px;
		}
		.text{
			font-size: 12px;
			display: inline-block;
			vertical-align: top;
		}
	}
}
</style>




